html,
body {
  margin: 0;
  overflow: hidden;
}

#menu {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

#menu button {
  background: transparent;
  border: 3px solid #FFF;
  color: #FFF;
  font-size: 24px;
  padding: 8px 24px;
}